}
.highContrast .nav-veilederpanel {
  background: #000000 !important;
}
.highContrast p,
.highContrast code,
.highContrast th,
.highContrast td,
.highContrast section,
.highContrast aside,
.highContrast article,
.highContrast header,
.highContrast footer,
.highContrast li,
.highContrast h1,
.highContrast h2,
.highContrast h3,
.highContrast h4,
.highContrast h5,
.highContrast h6,
.highContrast input,
.highContrast time,
.highContrast figcaption,
.highContrast select,
.highContrast textarea,
