  color: #0ca9e1 !important;
}
.highContrast .hero-link:focus,
.highContrast .hero-link:hover,
.highContrast .hero-link:active {
  border-color: transparent;
}
.highContrast label,
.highContrast input,
.highContrast select,
.highContrast textarea {
  color: #ffffff !important;
}
.highContrast .knapp,
.highContrast .knapp.hoved,
.highContrast .knapp.btn-auth,
.highContrast .btn {
  border: 1px solid #fff;
  color: #ffc40d !important;
  background-image: none;
}
.highContrast .knapp:focus,
.highContrast .knapp.hoved:focus,
.highContrast .knapp.btn-auth:focus,
.highContrast .btn:focus,
.highContrast .knapp:hover,
.highContrast .knapp.hoved:hover,
.highContrast .knapp.btn-auth:hover,
.highContrast .btn:hover,
.highContrast .knapp:active,
.highContrast .knapp.hoved:active,
.highContrast .knapp.btn-auth:active,
.highContrast .btn:active {
  border-color: #ffc40d;
  color: #ffc40d !important;
  text-decoration: underline;
}
.highContrast .tooltip {
  background: #ffffff !important;
  color: #000000 !important;
}
.highContrast .tooltip .tooltip-inner {
  background: #ffffff !important;
  color: #000000 !important;
}
.highContrast .tooltip .tooltip-arrow {
  border-right-color: #ffffff !important;
}
.highContrast .tooltip * {
  background-color: #ffffff !important;
}
.highContrast .tooltip span,
.highContrast .tooltip p,
.highContrast .tooltip div,
.highContrast .tooltip th,
.highContrast .tooltip td,
.highContrast .tooltip section,
.highContrast .tooltip aside,
.highContrast .tooltip article,
.highContrast .tooltip header,
.highContrast .tooltip footer,
.highContrast .tooltip li,
.highContrast .tooltip h1,
.highContrast .tooltip h2,
.highContrast .tooltip h3,
.highContrast .tooltip h4,
.highContrast .tooltip h5,
.highContrast .tooltip h6,
