}
.highContrast .hero-banner,
.highContrast .hero-banner * {
  background: #F1F1F1 !important;
}
.highContrast .siteheader .hero-banner.nighttime,
.highContrast .siteheader .hero-banner.nighttime * {
  background-color: #98B3B7 !important;
}
.highContrast ::-webkit-input-placeholder {
  color: #ffffff;
}
.highContrast ::-moz-placeholder {
  color: #ffffff;
}
.highContrast :-ms-input-placeholder {
  color: #ffffff;
}
.highContrast .dropdown-menu {
  border: 1px solid #ffffff;
}
.highContrast li.dropdown .caret {
  border-top: 4px solid #ffc40d;
}
.highContrast li.dropdown:hover .caret,
.highContrast li.dropdown:active .caret,
.highContrast li.dropdown:focus .caret {
  border-top: 4px solid #0ca9e1;
}
.highContrast .btn-small {
  background-color: transparent;
  background-image: none !important;
  border: 1px solid #d1d1d1;
}
.highContrast .btn-small:hover,
.highContrast .btn-small:active,
.highContrast .btn-small:focus {
  color: #ffc40d;
}
.highContrast .skjemaelement__input {
  border-width: 2px;
  border-color: #ffffff;
}
.highContrast .skjemaelement__input--harFeil {
