.ekspanderbartPanel {
  position: relative;
  background: white;
  border-radius: 0.25rem;
}
.ekspanderbartPanel:hover,
.ekspanderbartPanel--hover {
  box-shadow: #B7B1A9 0 2px 1px 0;
}
.ekspanderbartPanel:focus,
.ekspanderbartPanel--focus {
  box-shadow: 0 0 0px 3px #254b6d;
  outline: none;
}
.ekspanderbartPanel__hode {
  background: transparent;
  border: 0px;
  text-align: left;
  width: 100%;
  background-color: #FFF;
  display: block;
  padding: 1rem 1rem 1rem 1rem;
  border-radius: 4px;
  margin-bottom: 0px;
}
.ekspanderbartPanel__hode .ekspanderbartPanel__heading {
  color: #3E3832;
}
.ekspanderbartPanel__hode:hover,
.ekspanderbartPanel__hode--hover {
  cursor: pointer;
}
.ekspanderbartPanel__hode:hover .ekspanderbartPanel__heading,
.ekspanderbartPanel__hode--hover .ekspanderbartPanel__heading {
  text-decoration: underline;
  color: #0067C5;
}
.ekspanderbartPanel__hode:focus,
.ekspanderbartPanel__hode--focus {
  box-shadow: 0 0 0px 3px #254b6d;
  outline: none;
}
.ekspanderbartPanel__flex-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ekspanderbartPanel__innhold {
  padding: 1rem;
  padding-top: 0.5rem;
}
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator {
  display: inline-block;
  flex-shrink: 0;
  height: 2px;
  line-height: 2px;
  width: 20px;
  position: relative;
  text-align: center;
  transition: 200ms transform;
  vertical-align: middle;
}
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator:before,
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator:after {
  transition: 200ms transform;
  border-radius: 10px;
}
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  background: currentColor;
}
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  background: currentColor;
}
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator:before {
  transform: translateX(2px) rotate(45deg);
}
.ekspanderbartPanel--lukket .ekspanderbartPanel__indikator:after {
  transform: translateX(-2px) rotate(-45deg);
}
.ekspanderbartPanel--border {
  border: 1px solid #78706A;
}
.ekspanderbartPanel--border:hover,
.ekspanderbartPanel--border.panel-interaktiv-mixin--hover {
  border: 1px solid #0067C5;
}
.ekspanderbartPanel--border.ekspanderbartPanel--apen {
  border: 1px solid #78706A;
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.ekspanderbartPanel--apen .ekspanderbartPanel__innhold {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator {
  display: inline-block;
  flex-shrink: 0;
  height: 2px;
  line-height: 2px;
  width: 20px;
  position: relative;
  text-align: center;
  transition: 200ms transform;
  vertical-align: middle;
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator:before,
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator:after {
  transition: 200ms transform;
  border-radius: 10px;
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  background: currentColor;
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  background: currentColor;
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator:before {
  transform: translateX(2px) rotate(-45deg);
}
.ekspanderbartPanel--apen > .ekspanderbartPanel__hode .ekspanderbartPanel__indikator:after {
  transform: translateX(-2px) rotate(45deg);
}
.ekspanderbartPanel:hover.ekspanderbartPanel--apen {
  box-shadow: none;
}
.ekspanderbartPanel:hover.ekspanderbartPanel--apen .ekspanderbartPanel__hode {
  box-shadow: none;
}
.c-expandingpanel .ekspanderbartPanel__knapp {
  background: transparent !important;
  border: none !important;
}
.highContrast .c-expandingpanel {
  background: black;
  border: 2px solid white;
}
.highContrast .c-expandingpanel .ekspanderbartPanel__hode {
  background: black;
}
