._container {
  display: flex;
}
._container .eessi-pensjon-logo {
  width: 60px;
  height: 60px;
}
._container .center {
  display: flex;
  flex-direction: column;
  align-items: center;
}
._container aside {
  background: #f4f4f4;
  padding: 2rem 2rem;
}
._container aside ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
._container aside a {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  color: #0067C5;
  text-decoration: none;
  border-left: 2px solid transparent;
  position: relative;
  line-height: 1rem;
}
._container .body ul {
  margin: 0;
  padding: 0;
  padding-left: 1rem;
}
.highContrast._container {
  color: #ffc40d !important;
  background-color: #000000 !important;
}
.highContrast._container aside {
  background: #222222 !important;
}
