.c-tableSorter__status {
  min-height: 30px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  padding-left: 0.5rem;
}
.c-tableSorter__subcell {
  display: flex;
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
}
.c-tableSorter___seefilters-icon {
  cursor: pointer;
}
.c-tableSorter__pagination {
  justify-content: flex-end;
}
.c-tableSorter .header {
  cursor: pointer;
}
.highContrast tbody tr:nth-child(odd) {
  background: black;
}
.highContrast tbody tr:nth-child(even) {
  background: #333333;
}
.highContrast .tabell__tr--valgt td {
  background: orange !important;
  color: black;
}
.highContrast .tabell__tr--valgt td * {
  color: black;
}
